import { useState } from "react";

const districts = [
  "Thiruvananthapuram", "Kollam", "Pathanamthitta", "Alappuzha", "Kottayam",
  "Idukki", "Ernakulam", "Thrissur", "Palakkad", "Malappuram",
  "Kozhikode", "Wayanad", "Kannur", "Kasaragod"
];

export default function HospitalRegistrationForm() {
  const [formData, setFormData] = useState({});

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: files ? files[0] : value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(formData);
    // Send to API or backend
  };

  return (
    <form className="max-w-4xl mx-auto p-6 grid gap-4 bg-white shadow-xl rounded-xl" onSubmit={handleSubmit}>
      <h2 className="text-2xl font-bold">Hospital Registration Form</h2>

      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
        <input type="number" name="registrationNumber" placeholder="Registration Number" onChange={handleChange} required />
        <select name="district" onChange={handleChange} required>
          <option value="">Select District</option>
          {districts.map((d, i) => <option key={i} value={d}>{d}</option>)}
        </select>
        <input type="text" name="panchayat" placeholder="Panchayat" onChange={handleChange} required />
        <input type="text" name="ward" placeholder="Ward" onChange={handleChange} required />
        <input type="text" name="area" placeholder="Area/Root" onChange={handleChange} required />
        <input type="date" name="date" onChange={handleChange} required />
        <textarea name="diagnosis" placeholder="Diagnosis (max 1000 chars)" maxLength={1000} onChange={handleChange} required />
        <input type="text" name="category" placeholder="Category" onChange={handleChange} />
        <input type="text" name="name" placeholder="Name" onChange={handleChange} required />
        <input type="number" name="age" placeholder="Age" onChange={handleChange} required />
        <select name="gender" onChange={handleChange} required>
          <option value="">Select Gender</option>
          <option value="Male">Male</option>
          <option value="Female">Female</option>
          <option value="Other">Other</option>
        </select>
        <input type="text" name="spouseName" placeholder="Spouse Name" onChange={handleChange} />
        <input type="text" name="fatherName" placeholder="Father Name" onChange={handleChange} />
        <input type="text" name="motherName" placeholder="Mother Name" onChange={handleChange} />
        <textarea name="aadhaarAddress" placeholder="Aadhaar Address" onChange={handleChange} />
        <textarea name="currentAddress" placeholder="Current Address" onChange={handleChange} />
        <input type="text" name="phone1" placeholder="Phone Number 1" onChange={handleChange} required />
        <input type="text" name="phone2" placeholder="Phone Number 2" onChange={handleChange} />
        <input type="text" name="aadhaarNumber" placeholder="Aadhaar Number" onChange={handleChange} />
        <input type="text" name="contactPerson" placeholder="Contact Person" onChange={handleChange} />
        <input type="text" name="relation" placeholder="Relation" onChange={handleChange} />
        <input type="text" name="emergencyPhone" placeholder="Emergency Phone" onChange={handleChange} />
        <input type="text" name="neighbourResidence" placeholder="Neighbour Residence" onChange={handleChange} />
        <input type="text" name="neighbourPhone" placeholder="Neighbour Phone" onChange={handleChange} />
        <input type="text" name="volunteer" placeholder="Community Volunteer" onChange={handleChange} />
        <input type="text" name="volunteerPhone" placeholder="Volunteer Phone" onChange={handleChange} />
        <input type="text" name="wardMember" placeholder="Ward Member" onChange={handleChange} />
        <input type="text" name="wardPhone" placeholder="Ward Phone" onChange={handleChange} />
        <input type="text" name="aashaVolunteer" placeholder="Aasha Volunteer" onChange={handleChange} />
        <input type="text" name="aashaPhone" placeholder="Aasha Phone" onChange={handleChange} />
        <input type="text" name="otherPerson" placeholder="Other Person" onChange={handleChange} />
        <input type="text" name="otherPhone" placeholder="Other Phone" onChange={handleChange} />
        <textarea name="houseRoute" placeholder="House Route (max 1000 chars)" maxLength={1000} onChange={handleChange} />
        <label className="block">Photograph: <input type="file" name="photo" accept="image/*" onChange={handleChange} /></label>
        <label className="block">Aadhaar Document (PDF): <input type="file" name="aadhaarDoc" accept="application/pdf" onChange={handleChange} /></label>
      </div>

      <button type="submit" className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600">Submit</button>
    </form>
  );
}

